@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Moirai+One&display=swap);


:root {
  --color-white: #fff;
  --color-black: #000;
  --color-accent: #ffd9d9;
  --color-secondary: #5928e5;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Calibre", "Inter", "San Francisco", "SF Pro Text", -apple-system,
    system-ui, sans-serif;
  font-family: "Montserrat", sans-serif;
 

}
.container {
  --bs-gutter-x: 0 !important;
  --bs-gutter-y: 0 !important;
}
.container-fluid{
  --bs-gutter-x: 1.5rem !important;
  margin: 0% !important;
  padding: 0 !important;
}
#app {
  scroll-behavior: smooth;
  height: 100vh;
  scroll-snap-type: y mandatory;
  background-color: var(--color-black);
  color: var(--color-white) !important;
  
/* scroll-snap-stop: normal !important; */
  overflow-y: scroll;
}
.section {
  min-height: 100vh !important;
  scroll-snap-align: start;
 
}

/* width */
::-webkit-scrollbar {
  width: 0px;
}

/* Track */
::-webkit-scrollbar-track {
  /* background: #f1f1f1; */
}

/* Handle */
::-webkit-scrollbar-thumb {
  /* background: #888; */
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  /* background: #555; */
}


#project-container {
  display: grid !important;
  justify-content: center !important;
  align-items: center !important;
  grid-template-columns: repeat(auto-fit, minmax(19rem, 1fr));
  padding: 5.2rem !important;
  /* margin: 1rem !important; */
 
}
.card {
  margin: 0.3rem !important;
  background: rgb(89, 40, 213) !important;
  background: radial-gradient(
    circle,
    rgba(89, 40, 213, 1) 0%,
    rgba(89, 40, 229, 1) 35%,
    rgba(89, 40, 246, 1) 100%
  ) !important;
  overflow: hidden !important;
  position: relative !important;
}
.cover {
  height: 35vh !important;
  max-width: 100% !important;
  display: flex !important;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.cover span{
  color: black !important;
}
.cover h3 {
  font-weight: bold;
  color: black !important;
  margin-bottom: 10% !important;
}
.card:hover {
  transform: translateY(-4px) !important;
  transition: all 0.9s ease-out !important;
}
.card h3{
  transform: scale(1.3) !important;
  transition: all 0.3s ease-out !important;
}
.card:hover h6{
  transform: scale(1.3) !important;
  transition: all 0.3s ease-out !important;
}
.card:hover h3 {
  transform: scale(1.1) translateY(-20px) !important;
  opacity: 0 !important;
}
.card:hover h6{
  transform: scale(1.1) translateY(-20px) !important;
  opacity: 0 !important;
}
.card-body {
  height: 100% !important;
  position: absolute;
  /* top: 0%;
  left: 0%; */
  width: 100% !important;
  text-align: center !important;
  /* color: aliceblue !important; */
}
.card-title {
  opacity: 0 !important;
  transform: translateY(30px) !important;
  transition: all 0.3s ease-out !important;
}
.card-subtitle {
  opacity: 0 !important;
  transform: translateY(30px) !important;
  transition: all 0.3s ease-out 0.1s !important;
}
.card-text {
  opacity: 0 !important;
  transform: translateY(30px) !important;
  transition: all 0.3s ease-out 0.2s !important;
  /* white-space: nowrap !important; */
}
.card-link {
  opacity: 0 !important;
  transform: translateY(30px) !important;
  transition: all 0.3s ease-out 0.3s !important;
}
.card:hover .card-title {
  opacity: 1 !important;
  transform: translateY(0) !important;
}
.card:hover .card-subtitle {
  opacity: 1 !important;
  transform: translateY(0) !important;
}
.card:hover .card-text {
  transform: translateY(0) !important;

  opacity: 1 !important;
}
.card:hover .card-link {
  opacity: 1 !important;
  transform: translateY(0) !important;
}

:root {
